.hobbies {

    .hobbiesContent{
        border-radius: 10px;
        width: 100%;
        position: relative;
        min-height: 900px;
        height: 100%;
        overflow: hidden;
        background: #243465;
        border: 0.5rem solid #16203d;
        display: grid;

        grid-template-areas:
            "T T"
            "S M";
        grid-template-columns: 50% 50%;
        grid-template-rows: 15% 85%;

        h1 {
            grid-area: T;
            text-align: center;
            margin-top: 10px;
        }

        h2 {
            padding-left: 10px;
        }
        p {
            padding-left: 20px;
            padding-top: 10px;
            line-height: 30px;
        }

        .sport {
            border: 0.5rem solid hsl(225, 47%, 16%);
            grid-area: S;
            margin: 2px;
            border-radius: 10px;
            background-color: #2e3b83;
        }
        .music {
            border: 0.5rem solid #16203d;
            grid-area: M;
            margin: 2px;
            border-radius: 10px;
            background-color: #2e3b83;

            a {
                color: rgb(56, 240, 253);
            }
        }
    }
    
}

@media only screen and (max-width: 900px) {
    .hobbies {
        .hobbiesContent {
            grid-template-areas:
            "T"
            "S" 
            "M";
            
            grid-template-columns: 100%;
            grid-template-rows: 10% 45% 45%;
            h1 {
                font-size: 1.2em;
            }
        }
    }
}