.profile {

    .profileContent{
        border-radius: 10px;
        width: 100%;
        position: relative;
        min-height: 900px;
        height: 100%;
        overflow: hidden;
        text-align: center;
        background: #243465;
        border: 0.5rem solid #16203d;

        img {
            height: 200px;
            width: 200px;
        }

        h1 {
            margin-top: 10px;
        }
        p {
            h3 {
                display: inline-block;
                max-width: 600px;
                border-radius: 10px;
                background-color: #352a66;
                color: rgb(37, 255, 244);
                border: 0.5rem solid #373e50;
                padding: 2rem;
            }
        }
    }
}
@media only screen and (max-width: 600px) {
    .profile {
        .profileContent {
            img {
                height: 150px;
                width: 150px;
            }
            p {
                h3 {
                    font-size: smaller;
                }
            }
            h1 {
                font-size: 1.2em;
            }
        }
    }
}